.Wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6vw;
  min-height: 100vh;
}
.Wrapper .ImageWrapper {
  text-align: center;
}
.Wrapper .ImageWrapper .Image {
  width: 40vw;
}
.Wrapper .MainWrapper {
  flex: 1;
}

@media screen and (min-width: 769px) {
  .Wrapper {
    flex-direction: row;
  }
  .Wrapper .Image {
    width: 40vw;
  }
}